.priceWhole{
    height: 120vh;
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}

.pricec{
    display: flex;
    height: fit-content;
    width: fit-content;
    padding: 0;
}

.priceContainer{
    display: grid;
    place-items: center;
}

.priceWhole .containerh1{
    width: 100%;
    justify-content: center;
}

.priceWhole h1{
    display: flex;
    height: 10vh;
    width: 100%;
    justify-content: center;
    background: linear-gradient(to right,  #4105739d, #8435b590, #9e3784a7, #b8613ea8);
    border-radius: 30px 30px 0 0;
    font-size: 3vw;
    margin: 0;
    transform: translateY(2.25vh);
}

.priceWhole ul li{
    font-size: 1.5vw;
    padding: 0;
    margin: 1rem;
}

.priceWhole .containerText{
    display: flex;
    background-color: white;
    color: black;
    padding: 1rem;
    border-radius: 30px;
}

.priceWhole .priceDown{
    width: fit-content;
    background-color: #ad5f2b85;
    padding: 0 1.5rem;
    transform: translateY(-3vh);
    text-align: center;
}

.priceDown h2{
    font-size: 2vw;
}

@media only screen and (max-width: 900px) {
    .priceWhole{
        height: 80vh;
        width: 100vw;
        display: flex;
        justify-content: space-evenly;
        flex-direction: column;
        font-size: 200%;
    }

    .priceWhole ul li{
        font-size: 4.5vw;
        padding: 0;
        margin: 1rem;
    }
}